<template>
  <div class="container">
    <!-- sidebar -->
    <div class="sidebar hidden-sm-and-down">
      <div class="panel">
        <div class="head">Markdown </div>
        <div class="body">
          <ul>
            <li><tt>### </tt></li>
            <li><tt>****</tt></li>
            <li><tt>`console.log('')`</tt></li>
            <li><tt>```js\n code \n```</tt> </li>
            <li><tt>[]()</tt></li>
            <li><tt>![]()</tt></li>
          </ul>
        </div>
      </div>

      <div class="panel">
        <div class="head"></div>
        <div class="body">
            <p></p>
            <p> <a href="https://segmentfault.com/t/node.js" target="_blank">SegmentFault</a> </p>
        </div>
      </div>

    </div>
    <!-- sidebar end -->

    <!-- main -->
    <div class="main">

      <div class="panel">
        <div class="head">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item>{{ isCreate ? '' : '' }}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="body bg-fff pd-15">
          <!-- form -->
          <el-form ref="form" :model="form" :rules="rules">
            <el-form-item label="" prop="tab">
              <el-select v-model="form.tab">
                <el-option label="" value="dev"></el-option>
                <el-option label="" value="share"></el-option>
                <el-option label="" value="ask"></el-option>
                <el-option label="" value="job"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="title">
              <el-input v-model.trim="form.title" placeholder=" 10"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input type="textarea" id="markdown-editor"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveTopic()">{{ isCreate ? '' : '' }}</el-button>
            </el-form-item>
          </el-form>
          <!-- form end -->
        </div>
      </div>

    </div>
    <!-- main end -->
  </div>
</template>

<script>
import SimpleMDE from 'SimpleMDE'
import Axios from 'axios'
import API_CONFIG from '../../api/cnodejs.api'
import { mapState } from 'vuex'
export default {
  name: 'Release',
  data () {
    return {
      form: {
        tab: '', // 
        title: '' // 
      },
      rules: {
        tab: [
          {required: true, message: '', trigger: 'change'}
        ],
        title: [
          {required: true, message: '', trigger: 'blur'},
          {min: 10, message: '10', trigger: 'blur'}
        ]
      },
      isCreate: true //  :true
    }
  },
  computed: {
    ...mapState(['isLogin', 'accessToken'])
  },
  created () {
    if (this.$route.params.id === 'create') {
      this.isCreate = true // create
    } else {
      this.isCreate = false // update
      // 
      this.featchTopic()
    }
  },
  methods: {
    // 
    featchTopic () {
      Axios.get(`${API_CONFIG.topicDetail}${this.$route.params.id}`, {
        params: {
          mdrender: false
        }
      }).then(res => {
        this.form.title = res.data.data.title
        this.form.tab = res.data.data.tab
        this.simplemde.value(res.data.data.content)
      }).catch(e => {
        this.$message.error('')
        this.$router.replace('/')
      })
    },

    //  editor
    initMarkdownEdit () {
      this.simplemde = new SimpleMDE({
        element: document.getElementById('markdown-editor'),
        autoDownloadFontAwesome: false, // 
        spellChecker: false //
      })
    },

    // 
    saveTopic () {
      try {
        if (!this.isLogin) throw new Error('')
        if (!this.simplemde.value()) throw new Error('')
      } catch (e) {
        return this.$message.warning(e.message)
      }
      this.$refs['form'].validate((valid) => {
        // 
        Axios.post(this.isCreate ? API_CONFIG.cerateTopic : API_CONFIG.updateTopic, {
          accesstoken: this.accessToken,
          topic_id: this.isCreate ? undefined : this.$route.params.id,
          tab: this.form.tab,
          title: this.form.title,
          content: this.simplemde.value()
        }).then(res => {
          if (res.data.success) {
            this.$message.success(this.isCreate ? '' : '')
            this.$router.push({name: 'Topic', params: {id: res.data.topic_id}})
          }
        }).catch(e => e)
      })
    }
  },

  mounted () {
    this.initMarkdownEdit()
  }
}
</script>

<style>
.el-breadcrumb::after, .el-breadcrumb::before {
    display: inline-block;
    content: "";
}
.el-form-item::after, .el-form-item::before {
  display: inline;
  content: "";
}
.el-form-item__content::after, .el-form-item__content::before {
  display: inline;
  content: "";
}
</style>



